import Vue from 'vue'
import VueRouter from 'vue-router'
import InlineTable from '../components/CH0301InlineTable'
import CH0302ShrinkTable from '../components/CH0302ShrinkTable'
import CH0303IScroll from '../components/CH0303IScroll'
import CH0304BorderBox from '../components/CH0304BorderBox'
import DemoView from '../views/DemoView'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: DemoView,
    children: [
      {
        path: 'inline-table',
        name: 'InlineTable',
        component: InlineTable
      },
      {
        path: 'shrink-table',
        name: 'ShrinkTable',
        component: CH0302ShrinkTable
      },
      {
        path: 'i-scroll',
        name: 'IScroll',
        component: CH0303IScroll
      },
      {
        path: 'border-box',
        name: 'BorderBox',
        component: CH0304BorderBox
      }
    ]
  },

]

const router = new VueRouter({
  routes
})

export default router
